import React, {Component} from 'react';

import Aux from "../../hoc/Aux";
import Burger from '../../components/Burger/Burger';
import BuildControls from '../../components/Burger/BuildControls/BuildControls';

const INGREDIENT_PRICES = {
    salad: 1.5,
    cheese: 1,
    meat: 3.5,
    bacon: 2
}

class BurgerBuilder extends Component {

    constructor(props){
        super(props);
        this.state = {
            ingredients: {
                salad: 0,
                bacon: 0,
                cheese: 0,
                meat: 0
            },
            totalPrice: 0,
            currentTime: (new Date()).toLocaleString()
        }
    }

    addIgHandler = (type) =>  {
        const oldValue = this.state.ingredients[type];
        const newValue = oldValue + 1;
        const updatedValue = {
            ...this.state.ingredients
        };
        updatedValue[type] = newValue;

        const priceAdd = INGREDIENT_PRICES[type];
        const oldPrice = this.state.totalPrice;
        const upPrice = oldPrice + priceAdd;

        this.setState({totalPrice: upPrice, ingredients: updatedValue});

        console.log("Clicked add");
    }

    removeIgHandler = (type) => {
        const oldValue = this.state.ingredients[type];
        const newValue = oldValue - 1;
        if (newValue >= 0) {
            const updatedValue = {
                ...this.state.ingredients
            };
            updatedValue[type] = newValue;

            const priceAdd = INGREDIENT_PRICES[type];
            const oldPrice = this.state.totalPrice;
            const upPrice = oldPrice - priceAdd;

            this.setState({totalPrice: upPrice, ingredients: updatedValue});
        }

        console.log("Clicked remove");
    }

    countHowMany = (type) => {
        console.log("enter countHowMany" + type);
    }

    render () {
        return(
            <Aux>
                <Burger ingredients={this.state.ingredients} />
                <BuildControls 
                    clickedp={this.addIgHandler} 
                    clickedm={this.removeIgHandler}
                    number={this.countHowMany}
                    price={this.state.totalPrice} />
            </Aux>
        );
    }
}

export default BurgerBuilder;


