import React from 'react';

import css from './BuildControls.css';
import BuildControl from './BuildControl/BuildControl';

const controls = [
    {label: 'Salad', type: 'salad', price: 1.5},
    {label: 'Bacon', type: 'bacon', price: 2},
    {label: 'Cheese', type: 'cheese', price: 1},
    {label: 'Meat', type: 'meat', price: 3.5},
];

const buildControls = (props) => (
    <div className={css.BuildControls}>
        <p className={css.BuildControlsPara}>Current Price: {props.price}</p>
        {controls.map(ctrl => (
            <BuildControl 
                key={ctrl.label} 
                ilabel={ctrl.label} 
                number={() => props.number(ctrl.type)}
                priceThis={ctrl.price}
                clickedp={() => props.clickedp(ctrl.type)} 
                clickedm={() => props.clickedm(ctrl.type)} />
        ))}
    </div>
);

export default buildControls;


