import React from 'react';

import css from './BuildControl.css';

const buildControl = (props) => (
    <div className={css.BuildControl}>
        <div className={css.Label}>
            {props.ilabel}
            &nbsp;
            (Price: &nbsp; {props.priceThis})
        </div>
        <button className={css.Less} onClick={props.clickedm}>-1</button>
        &nbsp;
        <p>{props.number}</p>
        &nbsp;
        <button className={css.More} onClick={props.clickedp}>+1</button>
    </div>
);

export default buildControl;


